<template>
<div id="mapSection">
    <div v-show="false">{{ dist }}</div>
    <div id="map"></div>
</div>
</template>

<script>
import router from '@/router/index';
export default {
    props: ['pos', 'curLoca', 'range'],
    /* 
        txt:      url (ex, catProfile)
            => this.url
        pos:      [{pos_x,pos_y}, {pos_x,pos_y}, ...]
            => this.positions
        curLoca:  User 
            => this. user
        range:       
            => this. dist
    */
    data() {
        return {
            user: this.curLoca,
            // dist: this.range,
        }
    },
    computed: {
        positions() {
            return this.pos;
        },
        dist() {
            return this.range;
        },
        avgX() {
            let sumX = 0;
            let len = this.positions.length;
            for (var i = 0; i < len; i ++) {
                sumX += this.positions[i].pos_x;
            }
            return sumX / len;
        },
        avgY() {
            let sumY = 0;
            let len = this.positions.length;
            for (var i = 0; i < len; i ++) {
                sumY += this.positions[i].pos_y;
            }
            return sumY / len;
        },
    },
    mounted(){
        this.drawMap(this.dist, this.positions);
    },
    updated(){
        this.drawMap(this.dist, this.positions);
    },
    methods: {
        drawMap(dist, positions){
            const vm = this;
            var mapContainer = document.getElementById('map'); //   div 
            var mapOption = { 
                center: (this.user == undefined)?(new kakao.maps.LatLng(this.avgX, this.avgY)):(new kakao.maps.LatLng(this.user.lat, this.user.lng)), //  
                level: 5 //   
            };

            var map = new kakao.maps.Map(mapContainer, mapOption); //  
            // var map = new kakao.maps.StaticMap(mapContainer, mapOption); //  

            // var imageSrc = 'http://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_red.png', //      
            var markerImage = '';
            // var imageSrc = require(`@/assets/images/posts/${positions[0].image}`), //      
            var imageSrc = `/static/images/post/${positions[0].image}`, //      
            // var imageSrc = `/static/images/cat/${positions[0].no}.jpg`, //      
            imageSize = new kakao.maps.Size(50, 50), //  
            imageOption = {offset: new kakao.maps.Point(30, 30)}; //  .       .
            
            //      
            markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption);

            if(this.user != undefined){
                var circle = new kakao.maps.Circle({
                    map: map,
                    center : new kakao.maps.LatLng(this.user.lat, this.user.lng),  //    
                    radius: dist, //     
                    strokeWeight: 5, //   
                    strokeColor: '#75B8FA', //  
                    strokeOpacity: 1, //    1 0   0  
                    strokeStyle: 'dashed', //   
                    fillColor: '#CFE7FF', //  
                    fillOpacity: 0.7  //      
                });
            }
            //  
            // var marker = new kakao.maps.Marker({
            //     position: markerPosition, 
            //     image: markerImage, //   
            //     no: 1,
            // });

            //   
            

            for (var i = 0; i < positions.length; i ++) {
                //  
                if(i == 0){
                    var marker = new kakao.maps.Marker({
                        map: map, //   
                        position: new kakao.maps.LatLng(positions[i].pos_x, positions[i].pos_y), //  
                    });
                } else {
                    var marker = new kakao.maps.Marker({
                        map: map, //   
                        position: new kakao.maps.LatLng(positions[i].pos_x, positions[i].pos_y), //  
                    });
                }

                //  mouseover  mouseout  
                //      
                // for        
                kakao.maps.event.addListener(marker, 'click', makeOverListener(positions[i].no));
            }
            function makeOverListener(i) {
                return function() {
                    router.push(`/detailPost/${i}`);
                };
            }

            var linePath = [];
            positions.forEach(el => {
                linePath.push(new kakao.maps.LatLng(el.pos_x, el.pos_y));
            });
            var polyline = new kakao.maps.Polyline({
                path: linePath, //    
                strokeWeight: 5, //   
                strokeColor: '#FFAE00', //  
                strokeOpacity: 0.7, //    1 0   0  
                strokeStyle: 'solid' //  
            });
            polyline.setMap(map); 
        }
    }
}
</script>

<style lang="scss" scoped>
#map {
    width: 100%;
    height: 100%;
}
#mapSection {
    width: 100%;
    height: 100%;
}
</style>