<script>
//**** import ****/
  //  
import Modal from "@/components/post/modal/ModalAddPost.vue";
  //  
import LineChart from "./js/LineChart.js";
import BarChart from "./js/BarChart.js";
import DoughnutChart from "./js/DoughnutChart";
import HorizontalBarChart from "./js/HorizontalBarChart";
import { mapActions, mapMutations, mapGetters, mapState } from "vuex";
import PieChart from "./js/PieChart.js";

export default {
  components: {
    Modal: Modal,

    LineChart,
    BarChart,
    DoughnutChart,
    HorizontalBarChart,
    PieChart
  },
  computed: {
    ...mapGetters(['getLoginInfo',]),
    ...mapState("storeCat", ["test"]),
    ...mapGetters('storeUser/storeRank',['rankList',]),
    ...mapGetters(["getUserLoc"]),
    ...mapGetters("storeCat", [
      "catList",
      "selectedCat",
      "catSexArray",
      "catAgeArray",
      "catHealthArray",
      'myFollowingCatList'
    ]),
    ...mapGetters("storeUser", ["userList", "userSexArray", "userAgeArray"]),
    totalCatNum: function() {
      return this.catList.length; // {{ catList.length }}     .
    },
    totalUserNum: function() {
      return this.userList.length; // {{ userList.length }}     .
    },
    catSexChartData: function() {
      return {
        hoverBackgroundColor: "red",
        hoverBorderWidth: 20,
        labels: ["", "", " ", " ", "", ""],
        datasets: [
          {
            label: " // / ",
            backgroundColor: ["#41B883", "#E46651", "#00D8FF", "#028833","#FF6651","#0C88E8",],
            data: [
              this.catSexArray[0],
              this.catSexArray[1],
              this.catSexArray[2],
              this.catSexArray[3],
              0,    
              0
            ] //1,2,3,4] //
          },
          {
            label: "/ ",
            backgroundColor: ["#41B883", "#E46651", "#00D8FF", "#028833","#FF6651","#0C88E8",],
            data: [
              0,
              0,
              0,
              0,
              this.catSexArray[0] + this.catSexArray[1],
              this.catSexArray[2] + this.catSexArray[3]
            ] // 2,3] //
          }
        ]
      };
    },
    catNeuterChartData: function() {
      return {
        hoverBackgroundColor: "red",
        hoverBorderWidth: 20,
        labels: ["", ""],
        datasets: [
          {
            label: "/ ",
            backgroundColor: ["#FF6651","#0C88E8"],
            data: [
              this.catSexArray[0] + this.catSexArray[1],
              this.catSexArray[2] + this.catSexArray[3]
            ] // 2,3] //
          }
        ]
      };
    },
    catAgeChartData: function() {
      return {
        hoverBackgroundColor: "red",
        hoverBorderWidth: 20,
        labels: ["1", "2", "3", "4", "5", "6+"],
        datasets: [
          {
            label: "  ",
            backgroundColor: [
              "#41B883",
              "#E46651",
              "#00D8FF",
              "#00FFDA",
              "#00D9FF",
              "#0C88E8"
            ],
            data: [
              this.catAgeArray[0],
              this.catAgeArray[1],
              this.catAgeArray[2],
              this.catAgeArray[3],
              this.catAgeArray[4],
              this.catAgeArray[5]
            ]
          }
          // {
          //     label: "   ",
          //     backgroundColor: ["#41B883", "#E46651", ],
          //     data: [8, 4]
          // },
        ]
      };
    },
    catHealthChartData: function() {
      return {
        hoverBackgroundColor: "red",
        hoverBorderWidth: 20,
        labels: [" ", "  ", ""], //,"","",""
        datasets: [
          {
            label: "   ",
            backgroundColor: ["#41B883", "#E46651", "#00D8FF"], //"#00FFDA", "#00D9FF", "#0C88E8"],
            data: [
              this.catHealthArray[0],
              this.catHealthArray[1],
              this.catHealthArray[2]
            ] //this.catAgeArray[3], this.catAgeArray[4], this.catAgeArray[5]]
          }
          // {
          //     label: "   ",
          //     backgroundColor: ["#41B883", "#E46651", ],
          //     data: [8, 4]
          // },
        ]
      };
    },
    //   
    //   
    // totalUserNum: 10,
    userSexChartData: function() {
      return {
        hoverBackgroundColor: "red",
        hoverBorderWidth: 20,
        labels: ["", ""],
        datasets: [
          {
            label: "  ",
            backgroundColor: ["#41B883", "#E46651"],
            data: [4, 6]
            // data: [this.userSexArray[0], this.userSexArray[1]]
          }
          // {
          //     label: "   ",
          //     backgroundColor: ["#41B883", "#E46651", ],
          //     data: [8, 4]
          // },
        ]
      };
    },
    userAgeChartData: function() {
      return {
        hoverBackgroundColor: "red",
        hoverBorderWidth: 20,
        labels: ["0~9", "10", "20", "30", "40", "50+"],
        datasets: [
          {
            label: "  ",
            backgroundColor: [
              "#41B883",
              "#E46651",
              "#00D8FF",
              "#00FFDA",
              "#00D9FF",
              "#0C88E8"
            ],
            data: [
              1,
              2,
              4,
              1,
              1,
              1
            ]
            // data: [
            //   this.userAgeArray[0],
            //   this.userAgeArray[1],
            //   this.userAgeArray[2],
            //   this.userAgeArray[3],
            //   this.userAgeArray[4],
            //   this.userAgeArray[5]
            // ]
          }
          // {
          //     label: "   ",
          //     backgroundColor: ["#41B883", "#E46651", ],
          //     data: [8, 4]
          // },
        ]
      };
    },
    catRankChartData: function(rankList) {
        let rankChartData = [0,0,0,0],
            cnt = 0;
        let rankLen = 3;
        if (this.rankList != null){
            rankChartData = [];
            this.rankList.forEach(rank => {
                cnt += 1;
                if (cnt > rankLen) { // +1 ->  , +2 ->  , +3 -> , +4 -> , +5 -> for .. 
                    ;
                }else{
                    rankChartData.push(rank.rank_point)
                }
            });
        }
        while (cnt < rankLen) { //   0 !   
            cnt += 1;
            rankChartData.push(0)
        }

        if (true){ //      0 #$##$
            rankChartData.push(0)
        }else{
            rankChartData.push(0)
        }

        return {
            labels: [" ", "2 ", "3 ", " "], // 3   
            datasets: [
            {
                label: "Total  ",
                backgroundColor: "#f87979",
                // data: rankChartData //
                data: [rankChartData[0], rankChartData[1], rankChartData[2], rankChartData[3]]
                // data: [1,2,3,4]
            },
            // {
            //     label: "# of ( - )",
            //     backgroundColor: "#00D8FF",
            //     data: [30, 17, 14,0]
            // },
            // {
            //     label: "   ",
            //     backgroundColor: "#41B883",
            //     data: [20, 10, 8, 0]
            // }
            ]
        };
      },
    followCatHealthChartData: function() {
      let 
          totalHealthScore=0,
          checkNeuter=0,
          unhealthScore_hurt=0,
          unhealthScore_skin_disease=0;
      
      if (this.selectedCat.neuter === 1){
        checkNeuter = 1
      }else{
        checkNeuter = 0; 
      } 
      if (this.selectedCat.hurt === 1){
        unhealthScore_hurt = 1
      }else{
        unhealthScore_hurt = 0; 
      }
      if (this.selectedCat.skin_disease === 1){
        unhealthScore_skin_disease = 1
      }else{
        unhealthScore_skin_disease = 0; 
      }
      totalHealthScore=2-unhealthScore_hurt-unhealthScore_skin_disease + checkNeuter

      return {
        labels: [" ", "  ", "", ''], //,"","",""
        datasets: [
          {
            label: "  ",
            backgroundColor: ["#41B883", "#E46651", "#00D8FF", '#00FFDA'], //"#00FFDA", "#00D9FF", "#0C88E8"],
            data: [
                    totalHealthScore,
                    -unhealthScore_hurt,
                    -unhealthScore_skin_disease,
                    checkNeuter 
                  ]
          }
        ]
      };
    },
  },
  data() {
    return {
      // Test!
      //  
      selected_cat_no: '', //   1  ->   follow  1 ,  .


  //   &  
      showModalSelectCat: false,

      //   :
      //   
      //   
      //"    "
      datacollection: null,
      datacollection2: null,
      chartTitle: null, //   
      selectOption: 0, // locationKey: 0, 1, 2, ... 24 // : 0 ,  : 1 //   !
      selectedKey: "",

      chartOptions: {
        hoverBorderWidth: 20
      },
      // ! (   ! )
      chartData: {
        hoverBackgroundColor: "red",
        hoverBorderWidth: 10,
        //"   ()", "(// (/))", "/ ()",
        labels: ["", "", " ", " "],
        datasets: [
          {
            label: " //",
            backgroundColor: ["#41B883", "#E46651", "#00D8FF", "#028833"],
            data: [1, 10, 5, 30]
          },
          {
            label: "/ ",
            backgroundColor: ["#41B883", "#E46651", "#00D8FF", "#028833"],
            data: [1, 10, 5, 15]
          }
          // {
          //     label: "",
          //     backgroundColor: ["#41B883", "#E46651", "#00D8FF", "#028833"],
          //     data: [100, 10, 5,35]
          // }
        ]
      },

      // Vuetify 
      alignmentsAvailable: ["start", "center", "end", "baseline", "stretch"],
      alignment: "center",
      dense: false,
      justifyAvailable: [
        "start",
        "center",
        "end",
        "space-around",
        "space-between"
      ],
      justify: "center"
    };
  },
  created() {
    this.getCatList();
    this.getUserList();
    // this.getPostList();
    this.getMyFollowingCatList();

  },
  mounted() {
    this.getSelectedCat(this.selected_cat_no);
    // var location = " ",
    //   substring = "";
    // if (location.indexOf(substring) !== -1) {
    //   //      ..
    //   this.test_ = "!";
    // } else {
    //   this.test_ = "!";
    // }
  },
  watch: {
      selected_cat_no: function(){
        this.getRankList({cat_no: this.selected_cat_no});
        // this.getPostList({cat_no: this.selected_cat_no});
        this.getSelectedCat( this.selected_cat_no); // no      vuex     .
//#$#
      }
  },
  methods: {
    ...mapActions("storeCat", ["getCatList", "getSelectedCat",'getMyFollowingCatList']),
    ...mapActions("storeUser", ["getUserList"]),
    ...mapActions("storePost", ["getPostList"]),
    ...mapActions('storeUser/storeRank',['getRankList',]),
    tagCat(no) {
      //    
      this.selected_cat_no = no;
      // console.log("cat_no: " + no + ", cat_name: " + name + "  !!")
    },
  }
};
</script>

